// --------------------------------------------------
// 模块：企业数据分析平台
// 类名：Enterprise Data Analysis Platform 简写 edap
// --------------------------------------------------

// bootstrap 默认Less
@import "../bootstrap/variables.less";
@import "../bootstrap/mixins/clearfix.less";
@import "../bootstrap/mixins/forms.less";
// 思诺博UI Less
@import "../sinoui-variables.less";    // 变量
@import "../sinoui-mixins.less";       // 混入


.fixed-topbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1030;
    background-color: @brand-primary;
    .fixed-topbar-title {
        padding: 7px 5px 8px 5px;
    }
    .btn-group {
        .btn-link {
            padding: 8px 5px;
            color: @white;
            border-radius: 0;
            text-decoration: none;
            &.active,
            &:hover {
                background-color: rgba(255, 255, 255, .5);
            }
        }
    }
}
.mt-fixed-topbar {
    margin-top: 49px;
}
.item-title-block {
    padding: 5px 10px;
    line-height: 28px;
    overflow: hidden;
}
.sino-tabs-primary {
    &.sino-tabs-2 {
        .nav-tabs {
            > li {
                width: 50%;
                text-align: center;
            }
        }
    }
    .nav-tabs {
        border-bottom: 0;
        > li {
            border: 1px solid @brand-primary;
            > a {
                border-radius: 0;
                border: 0;
                &:hover {
                    background-color: transparent;
                    border: 0;
                }
            }
            &.active {
                background-color: @brand-primary;
                border-color: @brand-primary;
                > a {
                    &,
                    &:hover,
                    &:focus {
                        color: @white;
                        background-color: transparent;
                        border-bottom-color: transparent;
                        border: 0;
                        cursor: default;
                    }
                }
            }
        }
    }
    .tab-content {
        padding: 15px 10px;
        border-left: 1px solid @nav-tabs-border-color;
        border-right: 1px solid @nav-tabs-border-color;
        border-bottom: 1px solid @nav-tabs-border-color;
        &:extend(.clearfix all);
    }

    .search {
        .input-group {
            .form-control {
                min-width: 360px;
            }
            @media (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {
                .form-control {
                    min-width: 200px;
                }
            }
            .input-md {
                .input-size(32px; @padding-small-vertical; @padding-small-horizontal; @font-size-small; @line-height-small; 3px 0 0 3px);
            }
        }
        .input-group-addon {
            padding: 0;
            background-color: transparent;
            border: 0;
            border-radius: 0;
            .btn {
                border-radius: 0 @border-radius-base @border-radius-base 0;
                padding: 9px 6px;
            }
        }
    }
}

.breadcrumb-wrapper {
    /*border-radius: 0;
    margin-bottom: 0;*/
    background-color: @graylight;
    .breadcrumb {
        margin: 0 auto;
        padding-left: 10px;
        padding-right: 10px;
        background-color: transparent;
        > li {
            color: @gray;
            /*+ li:before {
                content: ">";
            }*/
            > a {
                color: @gray;
            }
            &.active {
                color: @gray-dark;
            }
        }
    }
}

.drag-wrapper {
    margin-left: -10px;
    margin-right: -10px;
    [class*="col-sm-"] {
        padding-left: 10px;
        padding-right: 10px;
    }
    .drag-item {
        margin-top: 10px;
        margin-bottom: 10px;
    }
    .drag-item-inner {
        border: 1px solid @gray-lighter;
        .drag-item-hd {
            position: relative;
            border-bottom: 1px solid @gray-lighter;
            .drag-item-title {
                display: block;
                margin: 0;
                padding-left: 15px;
                height: 36px;
                line-height: 36px;
                background-color: @graylight;
            }
            .drag-btn-group {
                margin-top: -36px;
                .dropdown-menu {
                    min-width: 60px;
                    margin-top: 36px;
                    padding: 0;
                    > li {
                        > a {
                            padding: 3px 12px;
                            i {
                                margin-right: 8px;
                            }
                        }
                    }
                }
            }
            .drag-item-more {
                position: absolute;
                right: 0;
                top: 0;
                width: 36px;
                height: 36px;
                text-align: center;
                .fa {
                    margin-top: 10px;
                    font-size: 18px;
                    color: @gray;
                }
            }
        }
        .drag-item-bd {
            height: 260px;
            padding: 10px;
            .echarts-item {
                z-index: 0;
                width: 100%;
                min-height: 260px;
            }
            &.drag-item-bd-md {
                height: 320px;
                .echarts-item {
                    min-height: 320px;
                }
            }
            &.drag-item-bd-additem {
                height: 297px;
                .flexbox();
                .align-items(center);
                .justify-content(center);
            }
        }
    }
}

.related-box {
    position: absolute;
    top: 0;
    left: 10px;
    right: 10px;
    height: 100%;
    .icon-check-circle {
        font-size: 50px;
        color: @white;
    }
    .dropdown-toggle {
        display: none !important;
    }
    &.related-box-active {
        .icon-check-circle {
            color: @brand-primary;
            &:before {
                content: "\e631";
            }
        }
        .dropdown-toggle {
            display: block !important;
        }
    }
    .related-box-inner {
        position: relative;
        height: 100%;
        background-color: rgba(0, 0, 0, .35);
        .flexbox();
        .align-items(center);
        .justify-content(center);
    }
    .dropdown {
        position: absolute;
        right: 5px;
        top: 5px;
    }
    .related-box-field {
        width: 360px;
        .related-box-field-l,
        .related-box-field-r {
            width: 140px;
        }
    }
}
.related-box-field {
    position: relative;
    width: 100%;
    padding: 10px;
    .related-box-close {
        position: absolute;
        right: 5px;
        top: 5px;
    }
    .related-box-field-hd {
        cursor: pointer;
    }
    .related-box-field-title {
        padding: 5px 0;
        .flexbox();
        .align-items(center);
    }
    .related-box-field-bd {
        //height: 260px;
        //overflow-y: auto;
    }
    .related-box-field-item {
        .flexbox();
        .align-items(center);
        &+.related-box-field-item {
            margin-top: 10px;
        }
        &:hover {
            .related-box-field-op {
                a {
                    display: block;
                }
            }
        }
    }
    .related-box-field-l,
    .related-box-field-r {
        width: ~"-webkit-calc(50% - 5px)";
        width: ~"-moz-calc(50% - 5px)";
        width: ~"calc(50% - 5px)";
    }
    .related-box-field-lt {
        width: 60px;
    }
    .related-box-field-rt {
        width: ~"-webkit-calc(100% - 70px)";
        width: ~"-moz-calc(100% - 70px)";
        width: ~"calc(100% - 70px)";
    }
    .related-box-field-l {
        margin-right: 5px;
    }
    .related-box-field-r {
        margin-left: 5px;
    }
    .related-box-field-op {
        width: 40px;
        margin-left: 10px;
        a {
            color: @gray-light;
            display: none;
        }
    }
}

.tagcloud {
    height: 240px;
    position: relative;
    overflow: hidden;
    a{
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        padding: 5px 15px;
        color: @white;
        font-size: @font-size-12;
        border-radius: 15px;
        background-color: #3aa1fe;
        text-decoration: none;
        &:hover {
            color: @white;
        }
    }
}

.yhs-info {
    padding: 15px 0;
    .flexbox();
    .align-items(center);
    .justify-content(center);
    .yhs-info-descr {
        margin-left: 20px;
        span {
            font-size: 26px;
        }
    }
}
.yhs-total {
    position: relative;
    padding-top: 20px;
    margin-top: 15px;
    .flexbox();
    .align-items(center);
    .justify-content(center);
    .hairline(top, @gray-lighter);
    .yhs-total-item {
        position: relative;
        width: 33.3333%;
        text-align: center;
        &+.yhs-total-item {
            .hairline(left, @gray-lighter);
        }
        span {
            font-size: @font-size-18;
        }
    }
}

.db-datalist {
    margin-top: 10px;
    padding-left: 0;
    li {
        width: 100%;
        margin-bottom: 1px;
        &:last-child {
            margin-bottom: 0;
        }
        a {
            position: relative;
            padding: 8px 10px;
            display: block;
            color: @gray-dark;
            border: 0;
            text-decoration: none;
            .item-title {
                width: 156px;
                display: inline-block;
                .form-control {
                    height: auto;
                    padding: 0 6px;
                    &[disabled] {
                        border-color: transparent;
                        background-color: transparent;
                        box-shadow: none;
                        cursor: pointer;
                    }
                }
            }
            .item-tags {
                background-color: @graygrey;
                color: @white;
                padding: 0 5px;
                border-radius: 2px;
                margin-left: 10px;
                &.active {
                    background-color: @brand-success;
                }
            }
            .item-handle {
                display: none;
                position: absolute;
                top: 4px;
                right: 0;
                padding: 5px;
                &.item-handle-sm {
                    padding-top: 2px;
                    padding-bottom: 2px;
                }
                i.icon {
                    font-size: @font-size-14;
                }
                i.fa {
                    width: 18px;
                    height: 18px;
                    font-size: @font-size-18;
                    text-align: center;
                    color: @gray;
                }
                .close {
                    font-size: 20px;
                }
                .tooltip-inner{
                    min-width: 200px;
                }
                .tooltip {
                    opacity: 1 !important;
                }
            }
        }
        a:hover,
        a:focus,
        &.active a {
            border: 0;
            background-color: @gray-lighter;
        }
        &.active {
            a:hover,
            a:focus {
                border: 0;
            }
        }
        a:hover {
            .item-handle {
                display: block;
            }
        }
    }
    &.nav-tabs {
        border: 0;
    }
    &.db-datalist-brand {
        li {
            &.active a {
                background-color: @brand-info;
                color: @link-color;
                //border-right: 3px solid @brand-primary;
                .hairline(right, @brand-primary);
                &:after {
                    width: 3px;
                }
            }
        }
    }
    .brand-title {
        margin-top: 0;
        margin-bottom: 0;
        padding: 10px 0;
        font-size: 14px;
        cursor: pointer;
        .icon {
            margin-right: 5px;
            font-size: @font-size-14;
        }
    }
    .brand-box {
        padding-left: 0;
        margin-bottom: 0;
        li {
            a {
                padding-left: 20px;
            }
            &.active {
                .item-handle {
                    display: block;
                }
            }
        }
    }
}
.db-poolitem {
    border: 1px solid @topbar-border-color;
    background-color: @white;
    &+.db-poolitem {
        margin-left: 15px;
    }
}
.db-poollog {
    margin-top: 10px;
    padding-left: 0;
    > li {
        padding: 5px 0;
    }
}

.project-name {
    .input-group-addon {
        background-color: @brand-primary;
        border-color: @brand-primary;
    }
    .icon {
        font-size: 22px;
        color: @white;
    }
    .form-control {
        height: 36px;
    }
}
.project-step-head {
    height: 50px;
    padding-top: 15px;
    .flexbox();
    .justify-content(space-between);
    .align-content(center);
    /*.hairline(bottom, @gray-lighter);
    &:after {
        bottom: 30%;
    }*/
    .step-item {
        position: relative;
        //background-color: @white;
        //z-index: 2;
        padding: 10px;
        .flexbox();
        .align-items(center);
        .hairline(bottom, @gray-lighter);
        &:after {
            bottom: 45%;
        }
        &:first-child,
        &:nth-child(2) {
            width: ~"-webkit-calc(50% - 54px)";
            width: ~"-moz-calc(50% - 54px)";
            width: ~"calc(50% - 54px)";
            &:after {
                width: auto;
                left: 20px;
                right: 0
            }
            .step-item-text {
                padding-left: 10px;
                padding-right: 10px;
                background-color: @white;
            }
        }
        &:last-child {
            width: 108px;
            .hairline-remove(bottom);
            .step-item-text {
                margin-left: 10px;
            }
        }
        &.step-item-active {
            .hairline-color(bottom, @brand-primary);
            .step-item-icon {
                background-color: @brand-primary;
                border-color: @brand-primary;
                i {
                    color: @white;
                }
            }
        }
    }
    .step-item-icon {
        width: 30px;
        height: 30px;
        text-align: center;
        border-radius: 100%;
        border: 1px solid @graygrey;
        z-index: 2;
        .flexbox();
        .align-items(center);
        i {
            font-size: 20px;
            color: @graygrey;
            width: 20px;
            margin: 0 auto;
        }
    }
    .step-item-text {
        height: 30px;
        line-height: 30px;
        //margin-left: 10px;
        z-index: 2;
    }
}
.project-step {
    position: relative;
    height: 66px;
    width: 80%;
    margin: 0 auto;
    padding-top: 14px;
    .flexbox();
    .justify-content(space-between);
    .align-content(center);
    .hairline(bottom, @gray-lighter);
    &:after {
        height: 3px;
        bottom: 60%;
        left: 20px;
        right: 20px;
        width: ~"-webkit-calc(100% - 40px)";
        width: ~"-moz-calc(100% - 40px)";
        width: ~"calc(100% - 40px)";
    }
    .step-item {
        display: block;
        z-index: 9;
        text-align: center;
        .icon {
            font-size: 10px;
            color: @graygrey;
        }
        &.step-item-active {
            .icon {
                color: @brand-primary;
            }
        }
        .step-item-icon {
            margin: 0 auto;
            width: 22px;
            display: block;
            background-color: @graylight;
        }
        .step-item-text {
            display: block;
            width: 100%;
        }
    }
    .step-progress {
        position: absolute;
        left: 20px;
        bottom: 60%;
        height: 3px;
        z-index: 3;
        background-color: @brand-primary;
        -webkit-transition: all 1s linear;
        transition: all 1s linear;
    }
}

.en-index {
    position: relative;
    margin-left: 20px;
    margin-right: 20px;
    padding: 8px 0;
    .hairline(bottom, @layout-border-color);
    a {
        display: inline-block;
        min-width: 12px;
        text-align: center;
    }
}
.db-table {
    position: relative;
    margin: 0 20px;
    padding: 8px 0;
    overflow: hidden;
    li {
        margin: 5px 15px 5px 0;
        float: left;
    }
    .table-item {
        .flexbox();
        .align-items(flex-end);
        a {
            padding: 3px 8px;
            background-color: @graylight;
            border: 1px solid @graydove;
            border-radius: 3px;
            display: inline-block;
            color: @gray;
            text-decoration: none;
            p {
                margin-bottom: 0;
                color: @graygrey;
                opacity: 1;
            }
        }
        &.table-item-active {
            a {
                background-color: @brand-primary;
                border-color: @brand-primary;
                color: @white;
                p {
                    color: @white;
                    opacity: .75;
                }
            }
            em {
                display: block;
            }
        }
        em {
            display: none;
            margin-left: 5px;
            padding: 0 3px;
            border-radius: 2px;
            font-style: normal;
            background-color: @graygrey;
            color: @white;
            cursor: pointer;
            &.active {
                background-color: @brand-success;
                border-color: @brand-success;
                color: @white;
            }
        }
    }
}
.db-databar {
    .flexbox();
    > .icon {
        font-size: 20px;
        cursor: pointer;
        &+.icon {
            margin-left: 10px;
        }
    }
    .db-databar-item {
        position: relative;
        padding-left: 10px;
        padding-right: 10px;
        .hairline(left, @nav-tabs-border-color);
        &:before {
            top: 20%;
            bottom: 20%;
            height: 60%;
        }
        &:first-child,
        &:last-child {
            &:before {
                background-color: transparent;
            }
        }
        &.db-databar-item-right {
            position: absolute;
            top: 5px;
            right: 0;
            padding-left: 0;
            padding-right: 0;
            border-radius: 3px;
            .dropdown-menu {
                .hairline(left, @gray-lighter);
                &:before {
                    left: 50%;
                }
                h3 {
                    position: relative;
                    margin: 0;
                    padding: 6px 10px;
                    border-bottom: 1px solid @dropdown-border;
                    background-color: @white;
                    z-index: 2;
                    .flexbox();
                    .align-items(center);
                    .justify-content(space-between);
                }
                > li {
                    position: relative;
                    margin: 10px 15px;
                    padding: 5px 10px;
                    border: 1px solid @gray-lighter;
                    border-radius: 20px;
                    text-align: center;
                    background-color: @white;
                    z-index: 2;
                    cursor: pointer;
                    &.active {
                        border-color: @brand-primary;
                        color: @brand-primary;
                        a {
                            color: @gray-dark;
                        }
                    }
                    > a {
                        display: none;
                        position: absolute;
                        right: -10px;
                        top: 5px;
                        width: 20px;
                        height: 20px;
                        border: 1px solid @gray-lighter;
                        border-radius: 100%;
                        background-color: @white;
                        &.icon {
                            padding: 0;
                            font-size: @font-size-12;
                        }
                    }
                    &:hover {
                        > a {
                            display: block;
                        }
                    }
                }
            }
        }
        > .icon {
            position: relative;
            padding-left: 10px;
            padding-right: 10px;
            font-size: 20px;
            cursor: pointer;
            &+.icon {
                margin-left: 0;
            }
        }
        .dropdown-menu {
            min-width: 112px;
            padding: 0;
            color: @gray-dark;
            .divider {
                margin: 2px 0;
            }
        }
    }
}
.db-search {
    &.db-search-pos {
        position: absolute;
        top: 7px;
        right: 10px;
    }
    &.db-search-auto {
        .form-group {
            .form-control {
                min-width: auto;
            }
        }
    }
    .form-group {
        position: relative;
        .form-control {
            min-width: 200px;
        }
        .icon {
            position: absolute;
            top: 1px;
            right: 1px;
            font-size: 16px;
            width: 26px;
            height: 26px;
            text-align: center;
            color: @gray-dark;
        }
    }
}

.associate-wrapper {
    margin: 20px;
    .flexbox();
    .align-items(center);
    .associate-item {
        width: 160px;
        .panel-default,
        .panel-info {
            > .panel-heading {
                padding: 0;
                border: 1px solid @gray-lighter;
                .flexbox();
                .align-items(center);
                .justify-content(space-between);
                .panel-op-title {
                    margin-left: 10px;
                    height: 30px;
                    line-height: 30px;
                    width: 86px;
                    font-weight: 700;
                    overflow:hidden;
                    text-overflow:ellipsis;
                    white-space:nowrap
                }
                .panel-op-add,
                .panel-op-edit {
                    display: inline-block;
                    width: 26px;
                    height: 30px;
                    line-height: 30px;
                    text-align: center;
                    text-decoration: none;
                }
            }
        }
        .panel-info {
            > .panel-heading {
                border: 1px solid @panel-info-border;
                color: @gray-dark;
            }
        }
        .list-group {
            border: 1px solid @gray-lighter;
            border-top-width: 0;
        }
        .list-group-item {
            padding: 6px 10px;
            margin-bottom: 0;
            border-width: 0 0 1px 0;
            border-bottom: 1px dashed @gray-lighter;
            overflow: hidden;
            span {
                height: 20px;
                line-height: 20px;
                overflow: hidden;
                display: block;
                width: 90px;
                float: left;
                overflow:hidden;
                text-overflow:ellipsis;
                white-space:nowrap
            }
            .icon {
                font-size: @font-size-12;
                float: right;
                width: 20px;
                text-align: center;
            }
        }
        .associate-box {
            width: 160px;
            &+.associate-box {
                margin-top: 30px;
            }
        }
        &.associate-item-primary {
            width: 240px;
            padding-left: 30px;
            .item-tags {
                background-color: @brand-success;
                color: @white;
                padding: 0 5px;
                border-radius: 2px;
            }
        }
        &+.associate-item {
            margin-left: 150px;

        }
    }
}

.modal-associate {
    .flexbox();
    .align-items(center);
    .justify-content(space-between);
    .associate-item {
        width: 185px;
        .panel-default,
        .panel-info {
            > .panel-heading {
                padding: 0;
                border: 1px solid @gray-lighter;
                font-weight: 700;
                .flexbox();
                .align-items(center);
                .justify-content(space-between);
                .panel-op-title {
                    margin-left: 10px;
                    height: 30px;
                    line-height: 30px;
                }
            }
        }
        .panel-info {
            > .panel-heading {
                border: 1px solid @panel-info-border;
                color: @gray-dark;
            }
        }
        .list-group {
            border: 1px solid @gray-lighter;
        }
        .panel-heading + .list-group {
            border-top-width: 0;
        }
        .list-group-item {
            padding: 6px 10px;
            margin-bottom: 0;
            border-width: 0 0 1px 0;
            border-bottom: 1px dashed @gray-lighter;
            .icon {
                font-size: @font-size-12;
                float: right;
                width: 20px;
                text-align: center;
            }
        }
        .associate-box {
            width: 185px;
            overflow: inherit;
            &+.associate-box {
                margin-top: 30px;
            }
        }
        &.associate-item-join {
            position: relative;
            width: 198px;
            padding-left: 58px;
            .hairline(top, @panel-default-border);
            &:before {
                top: 50%;
                z-index: 0;
            }
            .associate-box {
                width: 80px;
            }
            .list-group-item {
                span {
                    display: block;
                    width: 100%;
                    padding: 5px 0;
                    border: 1px solid @panel-default-border;
                    border-radius: 3px;
                    background-color: @panel-default-heading-bg;
                    text-align: center;
                    cursor: pointer;
                    + span {
                        margin-top: 10px;
                    }
                    &.active {
                        background-color: @brand-primary;
                        color: @white;
                        border-color: @brand-primary;
                    }
                }
            }
        }
        &.associate-item-other {
            .panel-heading {
                padding: 6px 10px;
            }
        }
    }
}

.analysis-wrapper {
    width: 100%;
    min-height: 124px;
    background-color: @graylight;
    border-top: 1px solid @gray-lighter;
    border-right: 1px solid @gray-lighter;
    border-bottom: 1px solid @gray-lighter;
    .analysis-item-inner-list {
        .flexbox();
        .align-items(stretch);
        > .analysis-item-box {
            width: ~"-webkit-calc(100% - 70px)";
            width: ~"-moz-calc(100% - 70px)";
            width: ~"calc(100% - 70px)";
        }
    }
    .analysis-item-andor,
    .analysis-item-andor-active {
        width: 70px;
        padding: 5px 10px;
        border-right: 1px solid @gray-lighter;
        border-bottom: 1px solid @gray-lighter;
        .flexbox();
        .align-items(center);
    }
    .analysis-item-andor-active {
        background-color: @brand-info;;
    }
    .analysis-item-box {
        width: 100%;
    }
    .analysis-item-field {
        padding: 5px 10px;
        border-bottom: 1px solid @gray-lighter;
        &:extend(.clearfix all);
        &:hover {
            .field-del {
                display: block;
            }
        }
        .field-group {
            width: 98%;
        }
        .field-input {
            width: 120px;
            &+.field-input {
                margin-left: 10px;
            }
            &.field-input-auto,
            &.field-input-name {
                width: auto;
                height: @input-height-base;
                padding: @padding-base-vertical @padding-base-horizontal;
                cursor: pointer;
                .btn.dropdown-toggle {
                    display: none;
                }
            }
            &.field-input-lg {
                width: 200px;
            }
            &.field-input-rule {
                width: ~"-webkit-calc(100% - 110px)";
                width: ~"-moz-calc(100% - 110px)";
                width: ~"calc(100% - 110px)";
                //padding: 4px 0;
                .form-control {
                    height: 28px;
                }
            }
            &.field-input-rows {
                width: ~"-webkit-calc(100% - 370px)";
                width: ~"-moz-calc(100% - 370px)";
                width: ~"calc(100% - 370px)";
            }
            .btn {
                padding-top: 3px;
                padding-bottom: 3px;
            }
            .icon-text {
                color: lighten(@brand-primary, 5%);
            }
            .icon-number {
                color: lighten(@brand-primary, 20%);
            }
            .icon-date {
                color: lighten(@brand-warning, 15%);
            }
        }
        .field-del {
            display: none;
            margin-top: 5px;
            font-size: @font-size-16;
            color: @gray-light;
        }
        .field-block {
            height: 36px;
            line-height: 36px;
            border: 1px dashed @brand-primary;
            background-color: @brand-info;
            .hairline(bottom, @gray-lighter);
        }
    }
}
.formula-wrapper {
    width: 600px;
    height: 520px;
    padding: 20px;
    .flexbox();
    .align-items(stretch);
    .formula-name {
        position: relative;
        width: 25px;
        padding-right: 5px;
        cursor: pointer;
        .hairline(right, @gray-lighter);
        i.fa {
            display: block;
            width: 20px;
            text-align: center;
            font-size: 20px;
        }
        span {
            display: block;
            width: 20px;
            text-align: center;
            writing-mode: lr-tb;
        }
    }
    .formula-function {
        position: relative;
        width: 170px;
        .hairline(right, @gray-lighter);
    }
    .formula-field {
        position: relative;
        width: 170px;
        .hairline(right, @gray-lighter);
    }
    .formula-rule {
        position: relative;
        width: 200px;
        color: @gray-light;
        &.formula-rule-lg {
            width: 370px;
        }
        .rule-bar {
            position: relative;
            height: 27px;
            .flexbox();
            .hairline(bottom, @gray-lighter);
            span {
                display: block;
                width: 20px;
                height: 20px;
                line-height: 20px;
                margin-left: 10px;
                color: @gray-dark;
                text-align: center;
                font-size: 22px;
                cursor: pointer;
            }
        }
        .rule-box {
            margin: 5px;
            height: ~"-webkit-calc(100% - 87px)";
            height: ~"-moz-calc(100% - 87px)";
            height: ~"calc(100% - 87px)";
            .CodeMirror {
                height: 100%;
                color: @gray-light;
                span[class*="#"] {
                    color: @brand-primary;
                }
                span[class*=fieldName] {
                    display: inline-block;
                    color: @brand-primary;
                    padding: 0 2px;
                    margin: 0 1px;
                    line-height: 16px;
                    background-color: @label-info-bg;
                }
            }
            &.rule-box-active {
                height: ~"-webkit-calc(50% - 44px)";
                height: ~"-moz-calc(50% - 44px)";
                height: ~"calc(50% - 44px)";
            }
            span {
                + span {
                    margin-left: 5px;
                }
            }
        }
        .rule-message {
            padding: 0 5px;
        }
        .rule-detail {
            padding: 5px;
            &.rule-detail-active {
                height: ~"-webkit-calc(50% - 44px)";
                height: ~"-moz-calc(50% - 44px)";
                height: ~"calc(50% - 44px)";
                word-wrap: break-word;
                overflow-y: auto;
            }
            p {
                margin-top: 5px;
            }
        }
        .rule-button {
            height: 30px;
            &.rule-button-active {
                padding-top: 11px;
            }
        }
        .rule-box-tips {
            position: absolute;
            left: 8px;
            top: 32px;
            line-height: 27px;
        }
    }
    .db-search {
        padding: 0 10px;
    }
    .function-item {
        padding: 0 10px;
        height: ~"-webkit-calc(100% - 37px)";
        height: ~"-moz-calc(100% - 37px)";
        height: ~"calc(100% - 37px)";
        overflow-y: auto;
    }
    .function-title {
        margin-top: 0;
        margin-bottom: 0;
        padding: 5px 0;
        cursor: pointer;
    }
    .function-box {
        padding-left: 0;
        margin-bottom: 0;
        li {
            a {
                padding: 2px 0 2px 20px;
                color: @gray-dark;
                text-decoration: none;
            }
            &:hover {
                cursor: pointer;
                background-color: @label-info-bg;
            }
        }
    }
    .tooltip-inner{
        min-width: 200px;
    }
    .tooltip {
        opacity: 1 !important;
    }
}

.data-left {
    position: relative;
    margin-left: 15px;
    margin-right: 15px;
    overflow: hidden;
    &+.data-left {
        .hairline(top, @nav-tabs-border-color);
    }
    ul {
        padding-left: 0;
        li {
            padding: 2px 0 2px 2px;
            i.icon {
                font-size: 14px;
                color: @btn-info-bg;
                margin-right: 5px;
            }
            &.active {
                background-color: @gray-lighter;
            }
            &+li {
                margin-top: 2px;
            }
        }
    }
}

.data-center {
    position: relative;
    h3 {
        margin-left: 15px;
    }
    ul {
        padding-left: 0;
        li {
        }
    }
    .chart-type {
        margin-left: 5px;
        margin-bottom: 0;
        overflow: hidden;
        li {
            float: left;
            width: 28px;
            height: 28px;
            line-height: 28px;
            margin-left: 10px;
            margin-bottom: 8px;
            border: 1px solid transparent;
            text-align: center;
            cursor: pointer;
            img {
                width: 24px;
                height: 24px;
                vertical-align: top;
                -webkit-filter: grayscale(100%);
