                -ms-filter: grayscale(100%);
                -o-filter: grayscale(100%);
                filter: grayscale(100%);
                filter: gray;
            }
            &.active {
                &.checked,
                &:hover {
                    border-color: @btn-info-bg;
                }
                img {
                    -webkit-filter: none;
