                    -ms-filter: none;
                    -o-filter: none;
                    filter: none;
                    filter: none;
                }
            }
        }
    }
    .chart-type-msg {
        margin-left: 15px;
        margin-right: 15px;
        color: @gray-light;
    }
}
.data-left,
.data-center {
    h3 {
        padding: 2px 0;
        margin-top: 10px;
        margin-bottom: 10px;
    }
}
.chart-attr {
    //padding: 10px 15px;
    .attr-title {
        padding: 5px 15px;
        overflow: hidden;
        cursor: pointer;
        &:hover {
            background-color: @brand-info;
        }
        i.fa {
            width: 16px;
            font-size: 20px;
            color: @gray-light;
        }
        span.icon {
            font-size: 16px;
            color: @gray-light;
        }
    }
    .attr-box {
