        &+.attr-box {
            .hairline(top, @nav-tabs-border-color);
        }
        .pane-attr {
            padding: 0 15px;
            margin-bottom: 10px;
            &.hide {
                margin-bottom: 0;
            }
        }
    }
    .attr-dropdown {
        border: 1px solid transparent;
        cursor: pointer;
        &:extend(.clearfix all);
        &:hover {
            border-color: @btn-info-bg;
        }
        &+.attr-dropdown {
            margin-top: 10px;
        }
        .dropdown-menu {
            top: 0;
            left: 100%;
