}
.chart-style {
    //padding: 10px 15px;
    .style-title {
        padding: 5px 15px;
        overflow: hidden;
        cursor: pointer;
        &:hover {
            background-color: @brand-info;
        }
        i.fa {
            width: 16px;
            font-size: 20px;
            color: @gray-light;
        }
    }
    .style-box {
        position: relative;
        &+.style-box {
            .hairline(top, @nav-tabs-border-color);
        }
        .pane-item {
            padding: 0 15px;
            margin-bottom: 10px;
            &.hide {
                margin-bottom: 0;
            }
        }
    }
}

.data-center {
    .chart-attr .pane-item {
        min-width: 200px;
    }
    .pane-item {
        .flexbox();
        .align-items(center);
        &+.pane-item {
            margin-top: 10px;
        }
        &.pane-item-top {
            .align-items(flex-start);
        }
        &.pane-item-end {
            .align-items(flex-end);
        }
        .pane-item-row {}
    }
    .pane-item-left {
        width: 60px;
        color: @gray-light;
        &.pane-item-left-sm {
            width: 40px;
        }
    }
    .pane-item-right {
        width: ~"-webkit-calc(100% - 60px)";
        width: ~"-moz-calc(100% - 60px)";
        width: ~"calc(100% - 60px)";
        &.pane-item-right-sm {
            width: ~"-webkit-calc(100% - 40px)";
            width: ~"-moz-calc(100% - 40px)";
            width: ~"calc(100% - 40px)";
        }
    }
    .pane-item-child {
        .flexbox();
        .align-items(center);
        .justify-content(space-between);
        .pane-child-left {
            width: ~"-webkit-calc(100% - 32px)";
            width: ~"-moz-calc(100% - 32px)";
            width: ~"calc(100% - 32px)";
        }
        .pane-child-right {
            width: 27px;
        }
    }
    .pane-textarea {
        position: relative;
        width: 100%;
        height: 60px;
        border: 1px solid @sino-form-border;
        &:hover {
            border-color: @btn-info-bg;
            cursor: pointer;
            i.icon {
                color: @btn-info-bg;
            }
        }
        i.icon {
            position: absolute;
            right: 5px;
            bottom: 6px;
            width: 20px;
            height: 20px;
            color: @sino-form-border;
        }
    }
}

